.simplecms__adminbar{display:flex;position:fixed;top:0;left:0;width:100%;height:40px;background:#192538;flex-flow:row nowrap;align-items:center;justify-content:space-between;box-sizing:border-box;padding:0 40px;z-index:10006}.simplecms__adminbar__logo{color:white;font-weight:bold;font-family:"Trebuchet MS", sans-serif;letter-spacing:0.02em;font-size:18px;padding-right:120px}.simplecms__adminbar__buttons{display:flex;flex-flow:row nowrap;align-items:center}.simplecms__adminbar__user-buttons{display:flex;flex-flow:row nowrap;align-items:center;justify-content:flex-end;height:100%;min-height:100%}.simplecms__adminbar__user-buttons--right{margin-right:15px}.simplecms__adminbar__button{padding:5px 25px;height:40px;background:#223550;color:#FBFBFC;font-weight:bold;font-family:"Trebuchet MS", sans-serif;text-transform:uppercase;letter-spacing:0.02em;font-size:14px;border:none;outline:none;cursor:pointer;text-decoration:none;display:flex;flex-flow:row nowrap;align-items:center;justify-content:center}.simplecms__adminbar__button:hover{background:#314d74}.simplecms__adminbar__button:last-child{margin-right:0}.simplecms__adminbar__button--save{display:none;background:#1E77F2}.simplecms__adminbar__button--save:hover{background:#0c5fd1}.simplecms__adminbar__button--logout{background:transparent;color:#FBFBFC;padding:0}.simplecms__adminbar__button--logout svg{font-size:inherit;width:14px}.simplecms__adminbar__button--logout:hover{background:transparent}.simplecms__auth-box{width:90%;margin-left:auto;margin-right:auto;max-width:320px;padding-top:50px}@media (min-width: 600px){.simplecms__auth-box__box{border-radius:4px}}.simplecms__auth-box__title{font-size:36px;letter-spacing:-0.2px;line-height:49px;font-weight:normal;margin:0 0 25px;padding:0;color:#223550;font-family:"Barlow","-apple-system","Helvetica","Ubuntu",sans-serif}.simplecms__auth-box__sub-title{font-size:18px;line-height:23px;font-weight:normal;font-family:"Barlow","-apple-system","Helvetica","Ubuntu",sans-serif;padding:5px 0;color:#838E9D}.simplecms__auth-box__form{margin:0;padding:0}.simplecms__auth-box__group{position:relative;margin-top:10px}.simplecms__auth-box__label{position:absolute;top:7px;left:16px;font-size:14px;font-weight:bold;color:#B2C2D6;text-transform:uppercase;font-family:"Barlow","-apple-system","Helvetica","Ubuntu",sans-serif;display:block;padding-bottom:3px;letter-spacing:0.9px}.simplecms__auth-box__input{color:#223550;border:1px solid #CCD4DB;border-radius:4px;font-family:"Barlow","-apple-system","Helvetica","Ubuntu",sans-serif;display:block;width:100%;padding:30px 16px 11px;box-sizing:border-box;overflow-x:auto;font-weight:500;font-size:16px}.simplecms__auth-box__input:focus{border-color:#1E77F2}.simplecms__auth-box__submit{border-radius:4px;background-color:#1E77F2;color:white;margin-top:20px;width:100%;padding:19px 30px;border:none;outline:none;cursor:pointer;transition:background-color 0.1s ease-in-out;font-family:"Barlow","-apple-system","Helvetica","Ubuntu",sans-serif;font-weight:500;font-size:18px}.simplecms__auth-box__submit:hover{background-color:#FA7035}.simplecms__auth-box__hr{width:20%;color:rgba(56,84,166,0.2);margin:30px auto 20px}.simplecms__auth-box__error{font-family:"Barlow","-apple-system","Helvetica","Ubuntu",sans-serif;font-size:14px;color:#99534B;background-color:rgba(153,83,75,0.2);border:1px solid #99534B;border-radius:4px;width:100%;padding:15px 20px;box-sizing:border-box;margin:10px 0 20px}html{margin-top:40px !important}body.simplecms__no-scroll{overflow:hidden}
